.gallery__photo {
	display: flex;
	flex-wrap: wrap;
}

.photo-title {
	display: flex;
	align-items: center;
	width: 40%;

	background-color: #afac9c;
}

.photo-title--show {
	display: none;
}

.photo-title p {
	width: 300px;
	margin: auto;
	padding: 19px 45px;

	color: #010101;
	font-weight: bold;

	font-size: 36px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-align: center;
	text-transform: uppercase;

	border: solid #121212 10px;
}

.photo-item {
	position: relative;

	width: 20%;
	margin: 0;
	padding: 0;

	transform: perspective(1000px); // vanilla-tilt

	transform-style: preserve-3d; // vanilla-tilt
}

.photo-item img {
	display: block;
	width: 100%;
	max-width: 100%;
}

.gallery-icon {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;

	transform: translateZ(20px); // vanilla-tilt
}

.gallery-icon-wrap {
	position: absolute;
	top: 0;
	left: 0;

	opacity: 0;

	&:hover {
		background-color: #ccc8b6a8;

		opacity: 1;
	}
}

.gallery-icon__item {
	cursor: pointer;

	&:first-child {
		position: absolute;
		top: 50%;
		left: 50%;

		width: 30px;
		height: 30px;
		margin-top: -15px;
		margin-left: -15px;
	}

	&:nth-child(2) {
		position: absolute;
		top: 70%;
		left: 25%;

		width: 20px;
		height: 26px;
		margin-top: -13px;
		margin-left: -10px;
	}

	&:nth-child(3) {
		position: absolute;
		top: 70%;
		left: 50%;

		width: 27px;
		height: 27px;
		margin-top: -14px;
		margin-left: -14px;
	}

	&:last-child {
		position: absolute;
		top: 70%;
		left: 75%;

		width: 28px;
		height: 24px;
		margin-top: -12px;
		margin-left: -14px;
	}
}

.swiper-button-prev,
.swiper-button-next {
	display: none;
}

.gallery-icon__item:hover path {
	fill: #8b623c;
}

@media (max-width: 1280px) {
	.photo-title {
		width: 50%;
	}

	.photo-item {
		width: 25%;
	}
}

@media (max-width: 768px) {
	.photo-title {
		display: flex;
		flex-basis: 100%;
		flex-shrink: 0;
		width: 100%;
		min-height: 300px;
	}

	.photo-title--show {
		width: 100%;
	}

	.photo-title--hide {
		display: none;
	}

	.photo-item {
		width: 33.3%;
	}

	.swiper-button-prev,
	.swiper-button-next {
		display: block;
	}
}

@media (max-width: 425px) {
	.gallery-icon__item {
		&:first-child {
			top: 40%;
		}

		&:nth-child(2) {
			left: 20%;
		}

		&:last-child {
			left: 80%;
		}
	}
}
