.team-wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.team-title {
	display: flex;

	align-items: center;
	width: 33.3333333333%;
	min-height: 500px;

	text-align: center;

	background-image: url('/assets/images/team-title-bg.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.team-title p {
	width: 300px;
	margin: auto;
	padding: 19px 85px;

	color: #010101;
	font-weight: bold;

	font-size: 36px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-transform: uppercase;
	// text-align: center;
	border: solid #121212 10px;
}

.team-person {
	width: 33.3333333333%;
	min-height: 500px;
}

.team-person--one {
	background-image: url('/assets/images/person-one.jpg');
	background-repeat: no-repeat;
	background-position: top center;
	background-size: cover;
}

.team-person__title {
	width: 33.3333333333%;
	padding: 0 50px 15px;

	background-color: #ccc8b6;
}

.team-person__title b {
	display: block;
	margin: 120px 0 45px;
	padding: 0;

	color: black;
	font-weight: bold;

	font-size: 24px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-align: center;
	text-transform: uppercase;
}

.team-person__title p {
	margin-bottom: 55px;

	color: black;

	font-size: 14px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-align: left;
}

.team-person--two {
	min-height: 500px;

	background-image: url('/assets/images/person-two.jpg');
	background-repeat: no-repeat;
	background-position: top center;
	background-size: cover;
}

.social-networks {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
}

.social-networks li {
	&:nth-child(1):hover path {
		fill: #3b5998;
	}

	&:nth-child(2):hover path {
		fill: #00acee;
	}

	&:nth-child(3):hover path {
		fill: #34526f;
	}

	&:nth-child(4):hover path {
		fill: #2867b2;
	}

	&:nth-child(5):hover path {
		fill: #c13584;
	}

	&:nth-child(6):hover path {
		fill: #0088cc;
	}
}

.social-networks a {
	display: block;

	cursor: pointer;
}

.newsletter {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;
	width: 33.3%;
	min-height: 500px;
	padding: 0 95px;

	background-color: #f2efe5;
}

.newsletter b {
	display: block;
	margin: 150px 0 5px;
	margin-bottom: 5px;
	padding: 0;
	padding-right: 50px;

	color: black;
	font-weight: bold;

	font-size: 14px;
	font-family: $font-secondary;
	line-height: 1.286;
	text-align: left;
	text-transform: uppercase;
}

.newsletter p {
	margin: 0;
	margin-bottom: 20px;

	color: black;

	font-size: 12px;
	font-family: $font-secondary;
	line-height: 1;
	text-align: left;
	text-transform: uppercase;
}

.newsletter__form {
	width: 100%;
}

.newsletter-email__label {
	display: block;
	height: 20px;
	margin-bottom: 10px;

	text-transform: uppercase;

	visibility: hidden;
}

.newsletter-email__input {
	width: 100%;
	min-width: 150px;
	height: 40px;
	margin-bottom: 20px;
	padding-left: 20px;

	color: #33333373;
	font-weight: bold;

	font-size: 12px;
	font-family: $font-secondary;
	line-height: 1.75;

	background-color: #f2efe5;

	border: 1px solid #121212;
}

.newsletter-email__input::placeholder {
	text-transform: uppercase;
}

.newsletter-email__input:focus {
	outline: none;
	box-shadow: 0 0 0 3px lightskyblue;
}

.newsletter-email__button {
	width: 100%;
	min-width: 150px;
	height: 40px;

	color: white;

	font-size: 12px;
	font-family: $font-secondary;
	line-height: 1.75;
	text-transform: uppercase;

	background-color: #9a977a;

	border: none;

	cursor: pointer;
}

.newsletter-email__button:hover {
	background-color: #aaaaaa;
}

.newsletter-email__button:focus {
	outline: none;
	box-shadow: 0 0 0 3px lightskyblue;
}

.newsletter-email__button:active {
	//border-color: #333; //если есть border
	color: #eeeeee;

	background-color: #333333;
}

.newsletter-email__button:disabled {
	//border-color: #333; //если есть border
	color: #eeeeee;

	background-color: black;
}

.red-newsletter {
	// background: #f05056;
	border: 1px solid #f05056;
}

.green-newsletter {
	// background: #66c1b0;
	border: 1px solid #66c1b0;
}

.team-quote {
	display: flex;
	flex-direction: column;
	min-height: 350px;

	margin: 0 auto;
	padding: 0 100px;

	background-image: url('/assets/images/team-promo-bg.jpg');
	background-repeat: no-repeat;
	background-position: top center;
	background-size: cover;

	& img {
		width: 43px;
		margin: 110px auto 35px;
	}

	& p {
		margin: 0 auto;

		color: #f2efe5;
		font-weight: bold;

		font-size: 22px;
		font-family: $font-secondary;
		font-style: italic;
		line-height: 1;
	}
}

@media (max-width: 1280px) {
	.newsletter {
		padding: 0 50px;
	}
}

@media (max-width: 1200px) {
	.social-networks a {
		transform: scale(.8);
	}
}

@media (max-width: 1024px) {
	.team-title {
		width: 100%;
	}

	.team-person {
		width: 50%;
	}

	.team-person__title {
		width: 50%;
	}

	.team-person__title--two {
		background-color: #f2efe5;
	}

	.newsletter {
		width: 100%;
		padding: 0 100px;

		background-color: #ccc8b6;
	}

	.team-quote {
		padding-bottom: 15px;
	}
}

@media (max-width: 768px) {
	.social-networks {
		// width: 150px;
	}

	.team-person {
		width: 100%;
	}

	.team-person__title {
		width: 100%;
		min-height: 500px;
	}

	.team-person__title--two {
		background-color: #ccc8b6;
	}

	.newsletter {
		padding: 0 50px;

		background-color: #f2efe5;
	}

	.team-quote {
		padding: 0 50px 50px;
	}
}

@media (max-width: 425px) {
	.newsletter {
		padding: 0 30px;
	}
}
