.preloader {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 20;

	width: 100%;
	height: 100%;

	background: white;
	visibility: visible;
	opacity: 1;

	transition: 1s all;

	.loader {
		position: absolute;
		top: 50%;
		left: 50%;

		width: 150px;
		height: 150px;

		border: 10px solid gray;
		border-top-color: black;
		border-radius: 50%;
		transform: translate(-50%, -50%);

		animation: 1s spin infinite linear;
	}

	&.done {
		visibility: hidden;
		opacity: 0;
	}
}

@keyframes spin {
	from {
		transform: translate(-50%, -50%) rotate(0deg);
	}

	to {
		transform: translate(-50%, -50%) rotate(360deg);
	}
}
