.page-header {
	max-width: 100%;
	min-height: 500px;

	background: url('/assets/images/single-slide.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.main-navigation {
	position: relative;

	display: flex;
	align-items: center;
	justify-content: center;
}

.main-navigation__logo {
	position: absolute;
	top: 30px;

	width: 260px;
	height: 137px;

	&:focus {
		outline: none;
		box-shadow: 0 0 0 3px lightskyblue;
	}

	&:active {
		// border-color: #333; // for buttons
		color: #eeeeee;

		background-color: #33333366;
	}
}

.main-navigation__list {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	margin-top: 65px;
}

.main-navigation__list a {
	display: block;
	margin: 10px;
	padding: 5px;

	color: $text-color;
	font-size: 16px;
	font-family: $font-secondary;
	line-height: 1.982;
	text-transform: uppercase;

	cursor: pointer;

	&:focus {
		outline: none;
		box-shadow: 0 0 0 3px lightskyblue;
	}

	&:active {
		// border-color: #333; // for buttons
		color: #eeeeee;

		background-color: #333333;
	}
}

.main-navigation__list a:hover {
	position: relative;

	&::after {
		position: absolute;
		top: 35px;
		left: 0;

		width: 100%;
		height: 2px;

		background: black;

		content: '';
	}
}

.main-navigation__list li:nth-child(3) {
	margin-right: 150px;
}

.main-navigation__list li:nth-child(4) {
	margin-left: 150px;
}

.main-navigation__navicon {
	display: none;

	visibility: hidden;
}

@media (max-width: 1024px) {
	.main-navigation__logo {
		top: 15px;

		// transform: scale(.8);
	}

	.main-navigation__logo img {
		transform: scale(.8);
	}

	.main-navigation__list {
		margin-top: 135px;
	}

	.main-navigation__list li:nth-child(3) {
		margin-right: 50px;
	}

	.main-navigation__list li:nth-child(4) {
		margin-left: 50px;
	}
}

@media (max-width: 768px) {
	.main-navigation {
		// background-color: #aeaeae;
		padding-bottom: 25px;

		background: none;

		transition: background .3s ease;
	}

	.main-navigation__logo {
		top: 15px;

		// transform: scale(.6);
	}

	.main-navigation__logo img {
		transform: scale(.6);
	}

	.main-navigation__list {
		flex-direction: column;
		margin-top: 145px;
		// display: none;
		transform: translateY(-200%);

		transition: transform .3s ease;
	}

	.main-navigation__list a {
		margin: 3px;
		padding: 3px;

		font-size: 22px;
	}

	.main-navigation__list li:nth-child(3) {
		margin-right: 0;
	}

	.main-navigation__list li:nth-child(4) {
		margin-left: 0;
	}

	.main-navigation__navicon {
		position: absolute;
		top: 50px;
		right: 5%;

		display: block;
		width: 33px;
		height: 33px;

		font-size: 0;

		background-color: transparent;
		border: none;
		outline: none;
		visibility: visible;
		cursor: pointer;
	}

	.main-navigation__icon-bar {
		position: absolute;
		top: 15px;
		left: 4px;

		display: block;
		width: 25px;
		height: 3px;

		background-color: black;
	}

	.main-navigation__icon-bar::before,
	.main-navigation__icon-bar::after {
		position: absolute;
		left: 0;

		width: 25px;
		height: 3px;

		background-color: black;

		transition: .3s;

		content: '';
	}

	.main-navigation__icon-bar::before {
		top: -9px;
	}

	.main-navigation__icon-bar::after {
		top: 9px;
	}

	.main-navigation__icon-bar--active::before {
		width: 25px;

		transform: rotate(45deg);
		transform-origin: left;

		transition: transform .3s ease;
	}

	.main-navigation__icon-bar--active::after {
		width: 25px;

		transform: rotate(-45deg);
		transform-origin: left;

		transition: transform .3s ease;
	}

	.main-navigation__icon-bar--active {
		background-color: transparent;

		transition: background-color .3s ease;
	}

	.main-navigation__list--show {
		transform: translateY(0%);

		transition: .3s;
	}

	.main-navigation--bg {
		background: #e7e5d0;

		transition: background-color .3s ease;
	}
}
