.page-footer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	max-width: 100%;
	min-height: 500px;
	padding: 0 10px;

	background-color: #aeaeae;
}

.page-footer__conclusion {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	padding-top: 115px;

	color: black;

	font-size: 12px;
	font-family: $font-secondary;
	line-height: 1.8;
	text-align: center;
}

@media (max-width: 768px) {
	.main-navigation__logo--footer {
		top: 15px;

		// transform: scale(1.5);
	}

	.main-navigation__logo--footer img {
		transform: scale(1.5);
	}

	.main-navigation__list--footer {
		display: none;
	}

	.main-navigation__navicon--footer {
		display: none;
	}

	.page-footer__conclusion {
		padding-top: 315px;
	}
}

@media (max-width: 425px) {
	.page-footer {
		padding: 0 50px;
	}

	.main-navigation__logo--footer {
		top: 15px;

		// transform: scale(1);
	}

	.main-navigation__logo--footer img {
		transform: scale(1);
	}
}
