.contact__wrap {
	display: flex;
	padding: 150px 0;

	background: #e7e5d0;
}

.contact-intro {
	width: 50%;
	padding: 0 50px 0 150px;
}

.contact-intro__title {
	width: 300px;
	margin: auto;
	margin-bottom: 80px;
	padding: 19px 20px;

	color: #010101;
	font-weight: bold;

	font-size: 36px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-align: center;
	text-transform: uppercase;

	border: solid #121212 10px;
}

.contact-intro__subtitle {
	margin: 0;
	margin-bottom: 70px;

	color: black;

	font-size: 18px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-align: center;
}

.contact-intro__communication {
	display: flex;
	justify-content: space-between;
	margin-bottom: 125px;
}

.info__title,
.info__phone,
.info__fax,
.info__email,
.info__adress,
.work__title,
.work__item {
	display: block;
	margin: 2px 0;

	color: black;

	font-size: 16px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-align: left;
	text-transform: uppercase;
}

.info__title,
.work__title {
	padding: 0;
	padding-bottom: 20px;
}

.info__email b,
.info__adress b {
	text-transform: none;
}

.form {
	display: flex;
	flex-direction: column;
	width: 50%;
	padding: 0 150px 0 50px;
}

.form__name-label,
.form__email-label,
.form__message-label {
	display: block;
	height: 20px;
	margin-bottom: 10px;

	text-transform: uppercase;

	visibility: hidden;
}

.form__name-input,
.form__email-input {
	width: 100%;
	height: 60px;
	margin-bottom: 30px;
	padding-left: 17px;
	// color: #3333336e;
	color: #33333373;

	font-size: 14px;
	font-family: $font-secondary;
	line-height: 1.714;

	background: #e7e5d0;

	border: 2px solid black;
	outline: none;
}

.form__name-input::placeholder,
.form__email-input::placeholder,
.form__message-textarea::placeholder {
	text-transform: uppercase;
}

.form__message-textarea {
	width: 100%;
	height: 220px;
	margin-bottom: 40px;
	padding-top: 17px;
	padding-left: 17px;

	color: #3333336e;

	font-size: 14px;
	font-family: $font-secondary;
	line-height: 1.714;

	background: #e7e5d0;

	border: 2px solid black;

	resize: none;
}

.form__name-input:focus,
.form__email-input:focus,
.form__message-textarea:focus {
	outline: none;
	box-shadow: 0 0 0 3px lightskyblue;
}

.form__message-textarea:focus {
	outline: none;
	box-shadow: 0 0 0 3px lightskyblue;
}

.form__button-contact {
	width: 100%;
	height: 60px;

	color: white;
	font-weight: bold;

	font-size: 14px;
	font-family: $font-secondary;
	line-height: 1.714;
	text-transform: uppercase;

	background: #9a977a;

	border: none;

	cursor: pointer;
}

.form__button-contact:hover {
	background-color: #aaaaaa;
}

.form__button-contact:focus {
	outline: none;
	box-shadow: 0 0 0 3px lightskyblue;
}

.form__button-contact:active {
	//border-color: #333; //если есть border
	color: #eeeeee;

	background-color: #333333;
}

.form__button-contact:disabled {
	//border-color: #333; //если есть border
	color: #eeeeee;

	background-color: black;
}

.red {
	// background: #f05056;
	border: 2px solid #f05056;
}

.green {
	// background: #66c1b0;
	border: 2px solid #66c1b0;
}

@media (max-width: 1480px) {
	.contact-intro {
		padding: 0 35px 0 70px;
	}

	.form {
		padding: 0 70px 0 35px;
	}
}

@media (max-width: 1280px) {
	.contact-intro__communication {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		margin-bottom: 50px;
	}

	.info {
		margin: 0 0 50px;
	}
}

@media (max-width: 900px) {
	.contact-intro {
		padding: 0 15px 0 45px;
	}

	.form {
		padding: 0 45px 0 15px;
	}
}

@media (max-width: 768px) {
	.contact__wrap {
		flex-direction: column;
	}

	.contact-intro {
		width: 100%;
		margin: 0 0 50px;
		padding: 0 25px;
	}

	.form {
		width: 100%;
		padding: 0 25px;
	}
}

@media (max-width: 375px) {
	.contact-intro {
		padding: 0 10px;
	}

	.form {
		padding: 0 10px;
	}
}
