@import 'general/variables';

html {
	height: 100%;
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	scroll-behavior: smooth;
	text-rendering: optimizeSpeed;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

img {
	max-width: 100%;
	height: auto;
}

a {
	text-decoration: none;
}

ul,
li {
	margin: 0;
	padding: 0;

	list-style: none;
}

p {
	margin: 0;
}

textarea {
	resize: none;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
	position: absolute;

	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	overflow: hidden;

	white-space: nowrap;

	border: 0;

	clip: rect(0 0 0 0);

	clip-path: inset(100%);
}

.wrapper {
	min-height: 100%;
	overflow: hidden;
}

.container {
	// padding: 0 10px;
	max-width: $content;
	margin: 0 auto;
}

body {
	background-color: black;
}
