.presentation__inner {
	display: flex;
}

.presentation-title {
	display: flex;
	flex-basis: 50%;
	align-items: center;
	min-height: 500px;

	text-align: center;

	background: url('/assets/images/studio-bg.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.presentation-title p {
	width: 300px;
	margin: auto;
	padding: 19px 45px;

	color: #010101;
	font-weight: bold;

	font-size: 36px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-transform: uppercase;

	border: solid #121212 10px;
}

.presentation-text {
	flex-basis: 50%;
	padding: 0 100px 15px;

	text-align: center;

	background-color: #f2efe5;
}

.presentation-text img {
	margin: 90px 0 25px;
}

.presentation-text__intro {
	margin-bottom: 25px;

	// color: #000000;
	font-size: 18px;
	font-family: $font-secondary;
	line-height: 1.2;
	text-align: center;
}

.presentation-text__desc {
	// color: #000000;
	font-size: 16px;
	font-family: $font-secondary;
	line-height: 1.2;

	text-align: left;
}

.presentation-picture {
	display: flex;
	flex-wrap: wrap;

	overflow: hidden; // vanilla-tilt

	background-color: black;
}

.picture__list {
	display: flex;
	flex-wrap: wrap;
}

.picture__item {
	position: relative;

	width: 16.6666666666%;

	transform: perspective(1000px); // vanilla-tilt

	transform-style: preserve-3d; // vanilla-tilt
}

.picture__item img {
	display: block;
	width: 100%;
	max-width: 100%;
}

.conclusion {
	min-height: 350px;

	background-image: url('/assets/images/tagline-bg.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.conclusion__quote {
	position: relative;

	margin: 0 0 60px;
	padding: 145px 60px 0;

	color: #f2efe5;
	font-weight: bold;

	font-size: 24px;
	font-family: $font-secondary;
	font-style: italic;
	line-height: 1.2;
	text-align: center;
}

.conclusion__quote::before,
.conclusion__quote::after {
	position: absolute;

	color: #f2efe5;

	font-size: 100px;
	font-family: $font-secondary;
	font-style: italic;
	line-height: 1.2;
	text-align: center;
}

.conclusion__quote::before {
	top: 40%;
	left: 15%;

	content: '“';
}

.conclusion__quote::after {
	top: 65%;
	right: 15%;

	content: '„';
}

.conclusion span {
	position: absolute;
	left: 67%;

	margin: 0 0 50px;

	color: #f2efe5;

	font-size: 18px;
	font-family: $font-secondary;
	font-style: italic;
	line-height: 1.2;
	text-transform: uppercase;
}

.icon-wrap {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;

	transform: translateZ(20px); // vanilla-tilt
}

.icon {
	position: absolute;
	top: 0;
	left: 0;

	opacity: 0;

	&:hover {
		background-color: #ccc8b6a8;

		opacity: 1;
	}
}

.icon__item {
	cursor: pointer;

	&:first-child {
		position: absolute;
		top: 50%;
		left: 50%;

		width: 30px;
		height: 30px;
		margin-top: -15px;
		margin-left: -15px;
	}

	&:nth-child(2) {
		position: absolute;
		top: 70%;
		left: 25%;

		width: 20px;
		height: 26px;
		margin-top: -13px;
		margin-left: -10px;
	}

	&:nth-child(3) {
		position: absolute;
		top: 70%;
		left: 50%;

		width: 27px;
		height: 27px;
		margin-top: -14px;
		margin-left: -14px;
	}

	&:last-child {
		position: absolute;
		top: 70%;
		left: 75%;

		width: 28px;
		height: 24px;
		margin-top: -12px;
		margin-left: -14px;
	}
}

.icon__item:hover path {
	fill: #8b623c;
}

.buttons {
	display: none;
}

@media (max-width: 1280px) {
	.presentation-text {
		padding: 0 55px 15px;
	}

	.picture__item {
		width: 25%;
	}

	.conclusion__quote::before {
		left: 4%;
	}

	.conclusion__quote::after {
		right: 4%;
	}
}

@media (max-width: 1024px) {
	.presentation-text {
		padding: 0 25px 25px;
	}

	.conclusion__quote::before {
		left: 0%;
	}

	.conclusion__quote::after {
		right: 0%;
	}
}

@media (max-width: 768px) {
	.presentation__inner {
		flex-direction: column;
	}

	.presentation-text {
		padding: 0 35px 35px;
	}

	.presentation-text img {
		margin: 60px 0 25px;
	}

	.picture__item {
		width: 33.333%;
	}

	.conclusion__quote {
		padding: 100px 60px 0;
	}

	.conclusion__quote::before {
		top: 12%;
	}

	.conclusion__quote::after {
		top: 70%;
	}
}

@media (max-width: 425px) {
	.presentation-picture {
		position: relative;

		display: block;
		width: 100%;
		height: 100%;
	}

	.picture__list {
		display: block;
		flex-wrap: nowrap;
		width: 100%;
		height: 100%;
		// overflow: hidden;
	}

	.picture__item {
		display: block;
		width: 0;

		opacity: 0;

		transition: opacity .4s ease-in-out;
	}

	.picture__item img.current {
		display: block;
		width: 100%;
		height: 100%;
	}

	.picture__item.current {
		display: block;
		width: 100%;

		opacity: 1;
	}

	.icon-wrap {
		display: none;
	}

	.buttons {
		display: block;
	}

	.prev,
	.next {
		position: absolute;

		margin-top: -20px;

		background: transparent;
		border: none;
		outline: none;
	}

	.prev {
		top: 50%;
		left: 5px;
	}

	.next {
		top: 50%;
		right: 5px;
	}

	.conclusion__quote {
		padding: 75px 60px 0;

		font-size: 20px;
	}

	.conclusion__quote::before,
	.conclusion__quote::after {
		font-size: 80px;
	}

	.conclusion__quote::before {
		top: 6%;
	}

	.conclusion__quote::after {
		top: 75%;
	}

	.conclusion span {
		left: 50%;
	}
}
